.better-rslider__container{width:100%;height:50px;background:burlywood;display:grid;position:relative}.better-rslider__container--vertical{width:50px;height:300px}.better-rslider__point-main{background:aquamarine;height:30px;width:30px;border-radius:15px;position:absolute;top:calc(50% - 15px);margin-left:-15px;z-index:3;display:grid;align-content:center;justify-content:center}.better-rslider__point-main:hover{cursor:pointer}.better-rslider__point-second{background:aquamarine;height:30px;width:30px;border-radius:15px;position:absolute;top:calc(50% - 15px);margin-left:-15px;z-index:2}.better-rslider__point-second:hover{cursor:pointer}.better-rslider__point--vertical{left:calc(50% - 15px);margin-bottom:-15px;margin-left:unset;top:unset}.better-rslider__range-line{background:purple;height:100%;position:absolute;z-index:1;left:0}.better-rslider__range-line--vertical{width:100%;bottom:0}.better-rslider__line{width:100%;height:50%;border-radius:10px;background:darkgrey;align-self:center;position:relative}.better-rslider__line--vertical{width:50%;height:100%;align-self:unset;justify-self:center}.main{display:grid;grid-template-columns:1fr 1fr;row-gap:20px;column-gap:20px;padding:100px}.main__item{display:grid;grid-auto-flow:row;row-gap:20px;height:fit-content}.settings{display:grid;grid-template-columns:100px 1fr;column-gap:10px;row-gap:5px}.settings>label{width:fit-content;display:grid;justify-self:end;align-self:center;text-transform:uppercase}
